<template>
    <div class="p-20">

        <div class="filter-container" style="margin-bottom:20px">
            <el-input class="filter-item" style="width: 300px;" placeholder="请输入用户ID、登录名、充值地址"
                      @keyup.native.enter="conditionQuery"
                      v-model="currencyTrans.keyword">
            </el-input>
            <span>虚拟币类型</span>
            <el-select clearable style="width: 150px" class="filter-item" v-model="currencyTrans.introduceId"
                       placeholder="全部类型">
                <el-option v-for="status in coinList" :key="status.baseSeq" :label="status.chineseName"
                           :value="status.baseSeq">
                </el-option>
            </el-select>
            <span class="demonstration">创建时间</span>
            <el-date-picker
                    v-model="dateTime"
                    type="datetimerange"
                    placeholder="选择时间范围"
                    range-separator="/"
                    @change="conditionChange">
            </el-date-picker>
            <el-button class="filter-item" @click="conditionQuery" size="small" style="margin-left: 30px">查询
            </el-button>
            <el-button type="primary" v-if="permission.export" @click="derivedData" size="small" style="margin-left: 20px">导出数据
            </el-button>
        </div>
        <div>
            <el-table
                    ref="singleTable"
                    :data="tableData"
                    v-loading="listLoading"
                    element-loading-text="给我一点时间">
                <el-table-column
                        type="index"
                        label="序号"
                        width="30">
                </el-table-column>
                <el-table-column
                        v-for="item in tableHeader"
                        :key="item.prop"
                        :label="item.label"
                        :prop="item.prop"
                        :width="item.width"
                        :min-width="item.minWidth"
                        :align="item.align"
                        :formatter="item.formatter"
                        show-overflow-tooltip>
                </el-table-column>
            </el-table>
            <div class="pagination">
                <el-pagination @size-change="userPageSize" @current-change="userPage"
                               :current-page="currencyTrans.page"
                               :page-sizes="[15,30,45, 60]" :page-size="currencyTrans.pageSize"
                               layout="total, sizes, prev, pager, next, jumper" :total="total">
                </el-pagination>
            </div>
        </div>
    </div>
</template>

<script>
    /*import fetch from '@/utils/fetch'*/
    import DCTP from '@/utils/dctp';
    import {currencytrans, educeExcel} from '@/api/capital';
    //启用禁用筛选条件
    const allCoin = {baseSeq: 0, chineseName: '全部类型', englishName: ""};
    const statuScope = [
        {key: 0, display_name: '0项确认'},
        {key: 1, display_name: '等待充值'},
        {key: 2, display_name: '等待审核'},
        {key: 3, display_name: '成功'},
        {key: 4, display_name: '用户撤销'},
    ];

    export default {
        data() {
            return {
                //搜索需求数据等
                allCoin,
                coinList: [],
                dateTime: '',
                tableData: [],
                total: null,
                currencyTrans: {
                    keyword: '',
                    introduceId: 0,
                    startTime: '',
                    endTime: '',
                    type: 1,
                    page: 1,
                    pageSize: 15
                },
                msg: '',
                listLoading: true,
                tableHeader: [
                    {
                        label: "会员UID",
                        prop: "userId",
                        width: 55
                    }, {
                        label: "登录名",
                        prop: "signtype",
                        width: 110,
                        align: 'center',
                        formatter(row, column, value) {
                            return value === 1 ? row.phone : (value === 2 ? row.email : '未知');
                        }
                    }, {
                        label: "真实姓名",
                        prop: "realName",
                        width: 65
                    }, {
                        label: "虚拟币类型",
                        prop: "currencyName",
                        width: 65,
                        align: 'center'
                    }, {
                        label: "交易类型",
                        prop: "type",
                        width: 80,
                        align: 'center',
                        formatter(row, column, value) {
                            return value === 1 ? '充值' : (value === 2 ? '提现' : '未知');
                        }
                    }, {
                        label: "数量",
                        prop: "amount",
                        width: 100,
                        align: 'left',
                    }, {
                        label: "状态",
                        prop: "status",
                        width: 65,
                        align: 'center',
                        formatter(row, column, value) {
                            let item = DCTP.utils.findDataByValue(statuScope, value, 'key');
                            return item ? item.display_name : '';
                        }
                    }, {
                        label: "确认次数",
                        // prop: "status",
                        width: 65,
                        align: 'center',
                        formatter(row, column, value) {
                            return row.confirmations + "次数"
                        }
                    }, {
                        label: "手续费",
                        prop: "fee",
                        align: 'center',
                        width: 45
                    }, {
                        label: "提现地址",
                        prop: "transAddress",
                        width: 265,
                        align: 'left'
                    }, {
                        label: "交易id",
                        prop: "txId",
                        width: 450,
                        align: 'left'
                    }, {
                        label: "创建时间",
                        prop: "createTime",
                        width: 170,
                        formatter(row, column, value) {
                            return DCTP.utils.parseDateTimeStr(value)
                        }
                    }, {
                        label: "最后更新时间",
                        prop: "updateTime",
                        width: 170,
                        formatter(row, column, value) {
                            return DCTP.utils.parseDateTimeStr(value)
                        }
                    }]
            }
        }
        ,
        computed: {
            tableDataLength() {
                return this.tableData.length;
            }
        }
        ,
        methods: {
            derivedData() {
                let url = 'api/admin/currencytrans/nofilter/export?type=1&keyword=';
                let excelurl = '';

                if (this.dateTime[0] === undefined || this.dateTime[0] === null) {
                    this.currencyTrans.endTime = '';
                    this.currencyTrans.startTime = '';
                    excelurl = url + this.currencyTrans.keyword + '&introduceId=' + this.currencyTrans.introduceId;
                } else {
                    excelurl = url + this.currencyTrans.keyword + '&introduceId=' + this.currencyTrans.introduceId + '&startTime=' + this.currencyTrans.startTime + '&endTime=' + this.currencyTrans.endTime;
                }
                location.href = excelurl;
            },
            deriveExcel() {

                educeExcel(this.currencyTrans).then(response => {

                })
            }
            ,
            //获取当前页数
            userPage(val) {
                this.currencyTrans.page = val;
                this.getList();
            },
            //获取每页多少条
            userPageSize(val) {
                this.currencyTrans.pageSize = val;
                this.getList();
            },//获取时间段
            conditionChange(val) {
                console.log(val);
                let times = val.split('/');
                this.currencyTrans.startTime = times[0];
                this.currencyTrans.endTime = times[1];
            },
            //条件查询方法
            conditionQuery() {
                if (undefined === this.dateTime[0] || null === this.dateTime[0]) {
                    this.currencyTrans.endTime = '';
                    this.currencyTrans.startTime = '';
                    this.getList();
                } else {
                    this.getList();
                }
            },
            getList() {
                this.listLoading = true
                currencytrans(this.currencyTrans).then(response => {
                    this.tableData = response.data.list;
                    this.coinList = response.data.coinList;
                    this.total = response.data.totalItems;
                    this.coinList.unshift(this.allCoin);
                    if (this.tableData.length === 0) {
                        this.msg = "未查询到此筛选数据";
                        this.$message({
                            type: 'warning',
                            message: this.msg
                        });
                    }
                    this.listLoading = false;
                })
            }
        }
        ,
        created() {
            this.getList();
        }
    }
</script>



